import { CloseCircleOutline } from "antd-mobile-icons";
import "../../../assets/css/head/assistant-face.css";
import logo from "../../../assets/img/logo.png";
import { brainStoreType } from "../../../assets/js/head/brainUtil";
function AssistantFace(props) {
    const assistantLogo = localStorage.getItem(brainStoreType.ASSISTANT_LOGO);
    return (
        <div id="assistant-face" className="assistant-face">
            <img src={!assistantLogo ? logo : assistantLogo} alt="assistant-face-logo" className={props.thinkStatus ? "assistant-face-logo animate-shadow-breath" : "assistant-face-logo"} />
            <div id="assistant-face-talk" className="assistant-face-talk">
                {
                    props.talkList.map((item, index) => (
                        <div className="assistant-face-talk-item flex-center" key={index}>
                            {item.talkContent}&nbsp;
                            <CloseCircleOutline className="assistant-face-talk-item-close" onClick={() => {
                                props.delTalk(item);
                            }} />
                        </div>
                    ))
                }
            </div>
        </div>
    );
}
export default AssistantFace;