import "../../../../assets/css/body/hand.css";
import ReactMarkdown from 'react-markdown';
import SyntaxHighlighter from "react-syntax-highlighter/dist/esm/default-highlight";
import { dark } from "react-syntax-highlighter/dist/esm/styles/hljs";
import { i18nUtil } from "../../../../assets/js/common/i18nUtil";
function AnswerFeature(props) {
    return (
        <div className="chat-answer" onClick={() => {
            props.close();
        }} style={{ display: props.show ? undefined : 'none' }}>
            <div className={(props.answerContent === i18nUtil.getText('thinking') + "...") ? "chat-answer-content animate-breath" : "chat-answer-content"} onClick={(e) => {
                e.stopPropagation();
            }}>
                <div className="chat-ask-text">{props.askContent}</div>
                <div id="chatAnswer" className="chat-answer-text">
                    <ReactMarkdown
                        children={props.answerContent}
                        components={{
                            code: ({ language, children }) => (
                                <SyntaxHighlighter language={language} style={dark}>
                                    {children}
                                </SyntaxHighlighter>
                            )
                        }} />
                </div>
            </div>
        </div>
    );
}
export default AnswerFeature;