import '../assets/css/assistant.css'
import AssistantFace from './head/face/AssistantFace';
import TextInput from './head/eyes/TextInput';
import FeaturePage from './body/hand/feature/FeaturePage';
import AnswerFeature from './body/hand/feature/AnswerFeature';
import { brainStoreType, brainStoreUtil, brainThinkUtil } from '../assets/js/head/brainUtil';
import { useEffect, useState } from 'react';
import { mouthUtil } from '../assets/js/head/mouthUtil';
import { commonUtil } from '../assets/js/common/commonUtil';
import { i18nUtil } from '../assets/js/common/i18nUtil';
function XAssistant() {
    const [answerContent, setAnswerContent] = useState("");
    const [askContent, setAskContent] = useState("");
    const [showAnswer, setShowAnswer] = useState(false);
    const [thinkStatus, setThinkStatus] = useState(false);
    const [chatRecords, setChatRecords] = useState([]);
    const [habitList, setHabitList] = useState([]);
    const [talkList, setTalkList] = useState([]);
    const [showAskRecord, setShowAskRecord] = useState(false);
    function loadNotification() {
        const notificationDataTypes = [brainStoreType.TODO_LIST];
        // 初始化通知定时器
        for (const dataType of notificationDataTypes) {
            const dataList = brainStoreUtil.getDataList(dataType);
            for (const data of dataList) {
                // 跳过已完成
                if (data.ok) {
                    continue;
                }
                if (data.okDates && data.okDates.indexOf(new Date().toLocaleDateString()) !== -1) {
                    continue;
                }
                // 重复的计算时间点
                if (data.repeatType) {
                    const nowDate = new Date();
                    const timeStrSplit = data.timeStr.split(":");
                    switch (data.repeatType) {
                        case 'everyday':
                            data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            break;
                        case 'workday':
                            if (nowDate.getDay() !== 0 && nowDate.getDay() !== 6) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            }
                            break;
                        case 'restday':
                            if (nowDate.getDay() === 0 || nowDate.getDay() === 6) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            }
                            break;
                        case 'sat':
                            if (nowDate.getDay() === 6) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            }
                            break;
                        case 'sun':
                            if (nowDate.getDay() === 0) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            }
                            break;
                        default: ;
                    }
                }
                if (!data.timestamp) {
                    continue;
                }
                const endMils = data.timestamp - new Date().getTime();
                if (endMils > 0) {
                    // 定时提醒
                    setTimeout(function () {
                        let content = "现在是" + new Date(data.timestamp).toLocaleTimeString() + " 你该" + data.title + "了";
                        if (!handleAlertData(dataType, data, content)) {
                            return;
                        }
                        setTimeout(() => {
                            commonUtil.scrollToLeft("assistant-face-talk");
                        }, 1000);
                    }, endMils);
                } else if (endMils <= 0) {
                    let content = "您有一条待办" + new Date(data.timestamp).toLocaleTimeString() + " " + data.title;
                    let handleAlertDataRes = handleAlertData(dataType, data, content);
                    if (!handleAlertDataRes) {
                        continue;
                    }
                }
            };
        }
        setTimeout(() => {
            commonUtil.scrollToLeft("assistant-face-talk");
        }, 1000);
    }
    function handleAlertData(dataType, data, content) {
        // 已删除，不提醒
        let newestData = brainStoreUtil.getById(dataType, data.dataId);
        if (!newestData) {
            return false;
        }
        if (newestData.ok) {
            return false;
        }
        if (newestData.okDates && newestData.okDates.indexOf(new Date().toLocaleDateString()) !== -1) {
            return false;
        }
        if (talkList.some(item => item.alertDataId === data.dataId)) {
            return false;
        }
        // 系统通知
        if (localStorage.getItem('appFront') !== true) {
        }
        mouthUtil.backTalk(content);
        // 本地通知
        // 更新 talkList
        setTalkList(prevTalkList => {
            // 检查是否已存在相同 alertDataId 的项
            if (prevTalkList.some(item => item.alertDataId === data.dataId)) {
                return prevTalkList; // 如果已存在，返回之前的列表
            }
            const newTalkList = [...prevTalkList, { alertDataId: data.dataId, alertDataType: dataType, alertDataTime: data.timeStr, talkContent: content }];
            return commonUtil.sortArray(newTalkList, 'alertDataTime');
        });
        brainStoreUtil.updateById(dataType, newestData);
        return true;
    }
    async function handleSendText(sendText) {
        if (!sendText || sendText === '') {
            return;
        }
        // 显示用户问题
        setAskContent(sendText);
        // 本地处理用户问题
        setAnswerContent(i18nUtil.getText("thinking") + "...");
        let localThinkRes = brainThinkUtil.localHandleText(sendText);
        setShowAnswer(true);
        if (localThinkRes && localThinkRes !== '') {
            // 存储对话
            const userMsgId = brainStoreUtil.add(brainStoreType.CHAT_LIST, { from: 'user', content: sendText });
            brainStoreUtil.add(brainStoreType.CHAT_LIST, { from: 'assistant', content: localThinkRes, userMsgId: userMsgId });
            // 更新页面对话
            setAnswerContent(localThinkRes);
            loadNotification();
            loadHabitList();
            loadChatRecords();
        } else {
            // 云处理用户消息
            setThinkStatus(true);
            let netThinkRes = await brainThinkUtil.netHandleText(sendText);
            // 存储对话
            const userMsgId = brainStoreUtil.add(brainStoreType.CHAT_LIST, { from: 'user', content: sendText });
            let loadingChatData = { from: 'assistant', userMsgId: userMsgId };
            if (netThinkRes && netThinkRes !== '') {
                loadingChatData.content = netThinkRes;
                loadingChatData.md = true;
            } else {
                loadingChatData.content = "不好意思，我还没学会";
            }
            brainStoreUtil.add(brainStoreType.CHAT_LIST, loadingChatData);
            // 更新页面对话
            setThinkStatus(false);
            setAskContent(sendText);
            setAnswerContent(loadingChatData.content);
            setShowAnswer(true);
            loadChatRecords();
            setTimeout(function () {
                commonUtil.scrollToTop('chatAnswer');
            }, 10);
        }
    }
    function loadChatRecords() {
        let chatList = brainStoreUtil.getDataList(brainStoreType.CHAT_LIST);
        let chatRecords = chatList.filter(chatItem => chatItem.from === 'user');
        chatRecords = commonUtil.sortArray(chatRecords, "createTime", false);
        setChatRecords(chatRecords);
    }
    function showChatRecord(record) {
        let chatList = brainStoreUtil.getDataList(brainStoreType.CHAT_LIST);
        let chatAnswer = chatList.filter(chatItem => chatItem.userMsgId === record.dataId)[0];
        if (!chatAnswer) {
            chatAnswer = { content: i18nUtil.getText('thinking') + "..." };
        }
        setAskContent(record.content);
        setAnswerContent(chatAnswer.content);
        setShowAnswer(true);
        setTimeout(function () {
            commonUtil.scrollToTop('chatAnswer');
        }, 10);
    }
    function loadHabitList() {
        let todoList = brainStoreUtil.getDataList(brainStoreType.TODO_LIST);
        todoList = todoList.filter(todo => {
            if (todo.repeatType) {
                let nowDay = new Date().getDay();
                switch (todo.repeatType) {
                    case 'everyday':
                        return true;
                    case 'workday':
                        if (nowDay !== 0 && nowDay !== 6) {
                            return true;
                        }
                        return false;
                    case 'restday':
                        if (nowDay === 0 || nowDay === 6) {
                            return true;
                        }
                        return false;
                    case 'sat':
                        if (nowDay === 6) {
                            return true;
                        }
                        return false;
                    case 'sun':
                        if (nowDay === 0) {
                            return true;
                        }
                        return false;
                    default: return false;
                }
            }
            return false;
        });
        todoList = commonUtil.sortArray(todoList, 'timeStr');
        setHabitList(todoList);
    }
    function delTalk(delItem) {
        setTalkList(talkList.filter((item) => item.alertDataId !== delItem.alertDataId));
        let alertData = brainStoreUtil.getById(delItem.alertDataType, delItem.alertDataId);
        if (!alertData.repeatType) {
            alertData.ok = true;
            brainStoreUtil.updateById(delItem.alertDataType, alertData);
        }
    }
    function initData() {
        loadNotification();
        loadChatRecords();
        loadHabitList();
    }
    useEffect(function () {
        initData();
        const handleVisibilityChange = () => {
            if (document.visibilityState === 'visible') {
                localStorage.setItem("appFront", true);
                initData();
            } else {
                localStorage.setItem("appFront", false);
            }
        };

        document.addEventListener('visibilitychange', handleVisibilityChange);

        return () => {
            document.removeEventListener('visibilitychange', handleVisibilityChange);
        };
        // eslint-disable-next-line
    }, []);
    return (
        <div className='assistant'>
            <div className='assistant-head'>
                <AssistantFace thinkStatus={thinkStatus} talkList={talkList} delTalk={(delItem) => delTalk(delItem)} />
            </div>
            <div className='assistant-main' onClick={() => {
                setShowAnswer(false);
                if (!showAnswer) {
                    setShowAskRecord(false);
                }
            }}>
                <AnswerFeature show={showAnswer} askContent={askContent} answerContent={answerContent} close={(value) => { setShowAnswer(false) }} />
                <FeaturePage show={true}
                    chatRecords={chatRecords} loadChatRecords={() => loadChatRecords()}
                    habitList={habitList} setHabitList={(value) => setHabitList(value)} loadHabitList={() => loadHabitList()}
                    showChatRecord={(record) => { showChatRecord(record); }} />
            </div>
            <div className='assistant-bottom'>
                <TextInput
                    show={true}
                    chatRecords={chatRecords}
                    loadChatRecords={() => loadChatRecords()}
                    showChatRecord={(record) => showChatRecord(record)}
                    showAskRecord={showAskRecord}
                    setShowAskRecord={(value) => setShowAskRecord(value)}
                    handleSendText={(sendText) => {
                        handleSendText(sendText);
                    }} />
            </div>
        </div>
    );
}
export default XAssistant;