import { DeleteOutline } from "antd-mobile-icons";
import { brainStoreType, brainStoreUtil } from "../../../../assets/js/head/brainUtil";

function AskRecordFeature(props) {
    return (
        <div className="chat-record animate-popUp" style={{ display: props.show ? undefined : 'none' }}>
            <div style={{ display: props.chatRecords.length !== 0 ? undefined : 'none' }} className="chat-record-list">
                {
                    props.chatRecords.map((record, index) => {
                        return (
                            <div key={index} className="chat-record-list-item flex-center">
                                <div className="chat-record-list-content flex-center" onClick={() => {
                                    props.showChatRecord(record);
                                }}>
                                    {record.content}
                                </div>
                                <DeleteOutline className="chat-record-list-content-del" onClick={() => {
                                    if (window.confirm('确定删除' + record.content + '吗？')) {
                                        brainStoreUtil.delById(brainStoreType.CHAT_LIST, record.dataId);
                                        props.loadChatRecords();
                                    }
                                }} />
                            </div>
                        );
                    })
                }
            </div>
            <div style={{ display: props.chatRecords.length === 0 ? undefined : 'none' }} className="chat-record-list">
                <div className="sys-empty">没有任何对话</div>
            </div>
        </div>
    );
}
export default AskRecordFeature;