import { CheckCircleFill } from "antd-mobile-icons";
import "../../../../assets/css/body/hand.css"
import { brainStoreType, brainStoreUtil } from "../../../../assets/js/head/brainUtil";
function HabitFeature(props) {
    function getHabitIcon(habitTile) {
        if (habitTile.indexOf('运动') !== -1) {
            return "🏃";
        }
        else if (habitTile.indexOf('杯水') !== -1) {
            return "🥤";
        }
        else if (habitTile.indexOf('起床') !== -1) {
            return "🛏️";
        }
        else if (habitTile.indexOf('睡觉') !== -1) {
            return "💤";
        }
        else if (habitTile.indexOf('蔬果') !== -1) {
            return "🍎";
        }
        else if (habitTile.indexOf('学习') !== -1) {
            return "📖";
        }
        else if (habitTile.indexOf('拓展') !== -1) {
            return "📚";
        }
        else if (habitTile.indexOf('兴趣') !== -1) {
            return "🎮";
        }
        else if (habitTile.indexOf('项目') !== -1) {
            return "🚀";
        }
        else if (habitTile.indexOf('实践') !== -1) {
            return "🛠️";
        }
        else if (habitTile.indexOf('习惯') !== -1) {
            return "⭐️";
        }
        else {
            return "🔁"
        }
    }
    function getHabitTotalOkDays() {
        let totalOkDays = 0;
        let dateOkMap = new Map();
        for (let habit of props.habitList) {
            if (habit.okDates) {
                for (let okDate of habit.okDates) {
                    if (dateOkMap.get(okDate)) {
                        dateOkMap.set(okDate, dateOkMap.get(okDate) + 1);
                    } else {
                        dateOkMap.set(okDate, 1);
                    }
                }
            }
        }

        dateOkMap.forEach((dateOkNumber, date) => {
            if (dateOkNumber === props.habitList.length) {
                totalOkDays += 1;
            }
        });
        return totalOkDays;
    }
    function handleHabitOk(habit, index) {
        let habitListTemp = [...props.habitList];
        if (!habit.okDates) {
            habit.okDates = [];
        }
        if (habit.okDates.indexOf(new Date().toLocaleDateString()) !== -1) {
            habit.okDates = habit.okDates.filter(date => date !== new Date().toLocaleDateString());
        } else {
            habit.okDates.push(new Date().toLocaleDateString());
        }
        habitListTemp[index] = habit;
        props.setHabitList(habitListTemp);
        brainStoreUtil.updateById(brainStoreType.TODO_LIST, habit);
    }
    return (
        <div className="feature-item-row">
            <div className="feature-item">
                <div className="feature-head">
                    <div className="feature-head-title">
                        习惯
                    </div>
                    <div className="feature-head-operate">
                        <div className="feature-head-operate-btn" style={{ display: props.habitList.length > 0 ? undefined : 'none' }}>已达标<span style={{ color: 'var(--primary-color)' }}>{getHabitTotalOkDays()}</span>天</div>
                    </div>
                </div>
                <div className="feature-content">
                    <div className="habit-list">
                        {
                            props.habitList.map((habit, index) => {
                                return (
                                    <div className="habit-item" key={index}>
                                        <div className="habit-item-icon flex-center" onClick={() => handleHabitOk(habit, index)}>
                                            {getHabitIcon(habit.title)}
                                            <span style={{ display: (habit.okDates && habit.okDates.indexOf(new Date().toLocaleDateString()) !== -1) ? undefined : 'none' }} className="habit-item-ok"><CheckCircleFill /></span>
                                        </div>
                                        <div onDoubleClick={() => {
                                            if (window.confirm('确定删除吗？')) {
                                                brainStoreUtil.delById(brainStoreType.TODO_LIST, habit.dataId);
                                                props.loadHabitList();
                                            }
                                        }}>
                                            <div className="habit-item-time flex-center">{habit.timeStr}</div>
                                            <div className="habit-item-title flex-center">{habit.title}</div>
                                            <div className="habit-item-stat flex-center">已坚持<span style={{ color: 'var(--primary-color)' }}>{habit.okDates ? habit.okDates.length : 0}</span>天</div>
                                        </div>
                                    </div>
                                );
                            })
                        }
                        <div style={{ display: props.habitList.length === 0 ? undefined : 'none' }} className="chat-record-list">
                            <div className="sys-empty">没有任何习惯</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
export default HabitFeature;